body {
  padding: 0;
  margin: 0 0;
  font-family: sans-serif;
  background-image: url(/Clicky-Game/static/media/background.a9e390e5.png);
  background-size: 100% 125%;
  background-repeat: no-repeat;
  background-position-y: 50px;
  
}
.navbar li {
  display: inline-block;
  text-align: center;
}

.itemLeft {
  width: 15%;
}

.itemCenter {
	width: 70%;
}

.itemRight {
  width: 15%;
}

.navbar {
  background: rgb(65, 65, 65);
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
}

li {
  color: white;
  padding: 15px 0;
}

body {
  font-size: 24px;
}

.header{
	background-color: rgba(128, 126, 126, 0);
	position: relative;
	width: 100%;
}

h1{
	text-align: center;
	font-size: 90px;
	font-weight: bold;
	padding: 90px 0 20px 0;
	color:white;
}

h2{
	text-align: center;
	font-size: 25px;
	font-weight: bold;
	padding-bottom: 40px;
	color: white;
}

.card {
    width: 200px;
    height: 150px;
    margin: 15px 45px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    border: 3px solid white;
  }
  
.wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 25px 0 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  
}

